import React from "react";

import { Row, Col } from "react-bootstrap";

import Cite from "./Cite";

import conclusion from "../content/conclusion.json"
import modernEcclesiastes from "../content/modernEcclesiastes.json"

class Conclusion extends React.Component {

    render() {
        return <><Row>
            <Col>
                <h2>{this.props.configuration.UI.conclusion[`${this.props.language}`]}</h2>
            </Col>
        </Row>
            <Row>
                <Col>
                    {conclusion.map((chunk, i) => {
                        let block
                        if (chunk.type == "paragraph") {
                            block = <p key={i} className="paragraph">{chunk.content[`${this.props.language}`]}</p>
                        } else {
                            let source = chunk.source[0]
                            let abbreviatedBook = this.props.configuration.books.find(book => book.id == source.book).name[`${this.props.language}`]
                            let sourceString = `${abbreviatedBook}`
                            if (source.chapter) {
                                sourceString = `${abbreviatedBook} ${source.chapter}`
                                if (source.verse) {
                                    sourceString = `${sourceString}, ${source.verse}`
                                }
                            }
                            block = <Cite key={i} source={sourceString} text={`${chunk.content[`${this.props.language}`]}`} />
                        }

                        return block
                    })}
                </Col>
            </Row>
            <Row>
            <Col>
                <h2>{this.props.configuration.UI.modernEcclesiastes[`${this.props.language}`]}</h2>
            </Col>
            <Row>
                <Col>
                {modernEcclesiastes.map((chunk, i) => {
                        let block
                        if (chunk.type == "paragraph") {
                            block = <p key={i} className="paragraph">{chunk.content[`${this.props.language}`]}</p>
                        } else {
                            let source = chunk.source[0]
                            let abbreviatedBook = this.props.configuration.books.find(book => book.id == source.book).name[`${this.props.language}`]
                            let sourceString = `${abbreviatedBook}`
                            if (source.chapter) {
                                sourceString = `${abbreviatedBook} ${source.chapter}`
                                if (source.verse) {
                                    sourceString = `${sourceString}, ${source.verse}`
                                }
                            }
                            block = <Cite key={i} source={sourceString} text={`${chunk.content[`${this.props.language}`]}`} />
                        }

                        return block
                    })}
                </Col>
            </Row>
        </Row>
        </>
    }
}

export default Conclusion