import React from "react";

import { Row, Col } from "react-bootstrap";

import introduction from '../content/introduction.json'

class Introduction extends React.Component {

    render() {
        return <><Row>
            <Col>
                <h2>{this.props.configuration.UI.introduction[`${this.props.language}`]}</h2>
            </Col>
        </Row>
            <Row>
                <Col>
                    <p className="paragraph">{introduction.find(chunk => chunk.id == 0).content[`${this.props.language}`]}</p>
                    <p className="paragraph">{introduction.find(chunk => chunk.id == 1).content[`${this.props.language}`]}</p>
                    <p className="paragraph">{introduction.find(chunk => chunk.id == 2).content[`${this.props.language}`]}</p>
                    <p className="paragraph">{introduction.find(chunk => chunk.id == 3).content[`${this.props.language}`]}</p>
                    <p className="paragraph">{introduction.find(chunk => chunk.id == 4).content[`${this.props.language}`]}</p>
                    <p className="paragraph">{introduction.find(chunk => chunk.id == 5).content[`${this.props.language}`]}</p>
                </Col>
            </Row>
        </>
    }
}

export default Introduction