import React from "react";

import { OverlayTrigger, Button, Tooltip, Pagination, Row, Col, Container } from "react-bootstrap";
import Introduction from "./Introduction";
import Conclusion from "./Conclusion";

class Viewer extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            pageNumber: 0,
            book: props.book,
            configuration: props.configuration
        }

        this.getPage = this.getPage.bind(this)
    }

    getPage(i) {
        let chapter = this.state.book.chapters[i - 1]
        let title = <h2>{chapter.title[`${this.props.language}`]}</h2>

        let verseNumber = 0

        let text = chapter.verses.map((verse, j) => {
            let tooltip = <Tooltip id="button-tooltip">
                {verse.comment[`${this.props.language}`]}
            </Tooltip>
            let renderedVerse

            let text = verse.text[`${this.props.language}`]
            let renderedLink = ""

            if (verse.links) {
                renderedLink = verse.links.map(link => {
                    let abbreviatedBook = this.state.configuration.books.find(book => book.id == link.book).abbreviazione[`${this.props.language}`]
                    return `${abbreviatedBook} ${link.chapter}, ${link.verse}`
                }).join("; ")
            }

            if (verse.comment[`${this.props.language}`]) {
                renderedVerse = <p key={j}><OverlayTrigger
                    placement="bottom"
                    delay={{ show: 250, hide: 400 }}
                    overlay={tooltip}>
                    <Button variant="warning">{text}</Button>
                </OverlayTrigger></p>
            } else {
                renderedVerse = <p key={j}>{text}</p>
            }

            if (verse.text[`${this.props.language}`] == "") {
                renderedVerse = null
            } else {
                verseNumber++
            }

            return {
                renderedVerse,
                renderedLink,
                renderedVerseNumber: verseNumber
            }
        })

        return {
            title: title,
            text: text
        }
    }

    render() {
        let pageSelector = [...Array(this.state.book.chapters.length + 2).keys()]

        pageSelector = pageSelector.map(i => {
            let renderedIndex = i
            if (i == 0) renderedIndex = this.state.configuration.UI.introduction[`${this.props.language}`]
            else if (i == this.state.book.chapters.length + 1) renderedIndex = this.state.configuration.UI.conclusion[`${this.props.language}`]
            let paginationItem = <Pagination.Item key={i} active={this.state.pageNumber === i} onClick={(event) => {
                this.setState({ pageNumber: i })
            }}>
                {renderedIndex}
            </Pagination.Item>

            return paginationItem
        })

        let renderedPage
        if (this.state.pageNumber == 0) {
            renderedPage = <Introduction configuration={this.props.configuration} language={this.props.language}/>
        }
        else if (this.state.pageNumber == this.state.book.chapters.length + 1) {
            renderedPage = <Conclusion configuration={this.props.configuration} language={this.props.language}/>
        } else {
            let page = this.getPage(this.state.pageNumber)
            renderedPage = <><Row>
                <Col md={{ span: 10, offset: 2 }}>{page.title}</Col>
            </Row>
                {renderedPage = page.text.map((row, i) => {
                    if (!row.renderedVerse) {
                        return null
                    }
                    return <Row key={i}>
                        <Col md={2}>
                            {row.renderedLink}
                        </Col>
                        <Col xs="auto">
                            <p className="verseNumber">{row.renderedVerseNumber}</p>
                        </Col>
                        <Col>
                            {row.renderedVerse}
                        </Col>
                    </Row>
                })}
            </>
        }

        return <>
            <Row>
                <Col>
                    <p style={{ textAlign: "center" }}>{this.state.configuration.UI.chapter[`${this.props.language}`]}</p>
                </Col>
            </Row>
            <Row>
                <Col />
                <Col md="auto">
                    <Container>
                    <Row><Col><Pagination style={{justifyContent: "center"}}>{pageSelector[0]}</Pagination></Col></Row>
                    <Row><Col><Pagination style={{justifyContent: "center"}}>{pageSelector.slice(1, 8)}</Pagination></Col></Row>
                    <Row><Col><Pagination style={{justifyContent: "center"}}>{pageSelector.slice(8, 13)}</Pagination></Col></Row>
                    <Row><Col><Pagination style={{justifyContent: "center"}}>{pageSelector[13]}</Pagination></Col></Row>
                    </Container>
                </Col>
                <Col />
            </Row>
            {renderedPage}
        </>
    }
}

export default Viewer