import logo from './logo.svg';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';

import React from 'react';
import { Container, Row, Col, Button, Offcanvas, DropdownButton, Dropdown } from 'react-bootstrap';

import ecclesiaste from './content/ecclesiaste.json'
import configuration from './content/configuration.json'

import Viewer from './components/Viewer';
import { getText, occurrences } from './components/Stats';

class App extends React.Component {

  constructor(props) {
    super(props)
    this.state = {
      show: false,
      language: "en"
    }

    this.onHide = this.onHide.bind(this)
    this.onShow = this.onShow.bind(this)
    this.changeLanguage = this.changeLanguage.bind(this)
  }

  extendLanguage(language) {
    return {
      it: configuration.UI.italian[`${language}`],
      en: configuration.UI.english[`${language}`]
    }[`${language}`]
  }

  onHide() {
    this.setState({ show: false })
  }

  onShow() {
    this.setState({ show: true })
  }

  changeLanguage(language) {
    this.setState({ language })
  }

  render() {

    let text = getText(ecclesiaste)
    return (
      <Container fluid>
        <Button variant="outline-secondary" id='settings' onClick={this.onShow}>
          {configuration.UI.settings[`${this.state.language}`]}
        </Button>
        <Offcanvas show={this.state.show} onHide={this.onHide} placement='end'>
          <Offcanvas.Header closeButton>
            <Offcanvas.Title>{configuration.UI.settings[`${this.state.language}`]}</Offcanvas.Title>
          </Offcanvas.Header>
          <Offcanvas.Body>
            <p>{configuration.UI.language[`${this.state.language}`]}</p>
            <DropdownButton title={this.extendLanguage(this.state.language)} onSelect={event => this.changeLanguage(event)}>
              <Dropdown.Item as="button" eventKey="it">{this.extendLanguage("it")}</Dropdown.Item>
              <Dropdown.Item as="button" eventKey="en">{this.extendLanguage("en")}</Dropdown.Item>
            </DropdownButton>
          </Offcanvas.Body>
        </Offcanvas>
        <Row>
          <Col>
            <h1>{ecclesiaste.title[`${this.state.language}`]}</h1>
          </Col>
        </Row>
        <Row>
          <Col>
            <Viewer book={ecclesiaste} configuration={configuration} language={this.state.language} />
          </Col>
        </Row>
      </Container>
    );
  }
}

export default App;
