
function occurrences(str) {
    const articles = ["il", "lo", "la", "i", "gli", "le"]
    const prepositions = ["di", "a", "da", "in", "con", "su", "per", "tra", "fra"]
    const undeterminedArticles = ["un", "uno", "una"]
    const compoundPrepositions = ["dei", "del", "dell", "delle", "della", "al", "degli"]
    const conjunctions = ["e", "anche", "perché", "ma", "poiché", "come", "infatti"]
    const demonstrativeAdjectives = ["questo", "questa", "questi", "queste"]
    const possessiveAdjectives = ["suo", "sua", "sue", "suoi"]
    const ignoredWords = ["è", "sono", "non", "l", "c", "se", "che", "chi", "ha", "ho", "", "si", ...articles, ...prepositions, ...undeterminedArticles, ...compoundPrepositions, ...conjunctions, ...demonstrativeAdjectives, ...possessiveAdjectives]

    let words = str
        .replace(/[\.,;:«»]/g, "")
        .replace(/'/g, " ")
        .split(" ")
        .filter(word => !ignoredWords.includes(word))

    let occurrences = {}
    words.forEach(word => {
        if (!occurrences[`${word}`]) {
            occurrences[`${word}`] = 1
        } else {
            occurrences[`${word}`]++
        }
    })

    occurrences = Object.keys(occurrences).map(word => { return { "word": word, "count": occurrences[`${word}`] }})
    occurrences.sort((occurrence1, occurrence2) => occurrence2.count - occurrence1.count)

    return occurrences
}

function getText(book) {
    return book
        .chapters
        .map(chapter => chapter
            .verses
            .map(verse => verse.text.it)
            .join(" "))
        .join(" ")
        .toLowerCase()
}

export { occurrences, getText }