import React from "react";

class Cite extends React.Component {

  render() {
    return <>
      <div>
        {this.props.text.split("\n").map((row, i) => <cite key={i} className="paragraph scripture-text">{row}<br /></cite>)}
        <p style={{ textAlign: "right" }}>({this.props.source})</p>
      </div>
    </>
  }
}

export default Cite